#section2 {
  display: flex;  
  justify-content: center;
  max-width: 90%;
  color: white;
  margin: auto;

  #leftColumn {
  background-image: url("../Images/Star-Decoration.png");
  background-repeat: no-repeat;
  background-size: 32%;
  background-position-x: 87%;
  background-position-Y: 12%;
  margin-left: 5%;
  margin-top:5%;
  }
  h3 {
    color: #d46453;
    font-weight: 400;
    letter-spacing: 4px;
    margin-bottom: 30px;
  }
  h2 {
    margin: 0;
    color: white;
    font-weight: 200;
    font-size: 68px;
    margin-bottom: 2%;
    line-height: 120%;
    z-index: 100;
    margin-bottom: 30px;
  }
  p {
    color: #bfbfbf;
    margin-bottom: 2.5%;
    line-height: 180%;
    margin-bottom: 35px;
  }
  span {
    color: #ffffff;
    font-weight: bolder;
    margin-left: 2%;
  }

  button {
    margin-bottom: 100px;
  }

  #selfieMetaContainer {
    display: flex;
    align-items: center;
    img {
      width: 50px;
      height: 50px;
    }
    p {
      margin-top: 7%;
      margin-left: 1.5%;
    }
  }
  #buttonLeft,
  #buttonRight {
    cursor: pointer;
    color: white;
    border: none;
    padding: 15px 20px 15px 20px;
    transform: scale(1);
    transition: 0.3s;
    font-size: 15px;
  }
  #buttonLeft:hover,
  #buttonRight:hover {
    transform: scale(1.1);
  }
  #buttonLeft:hover {
  background-color: #F7CC47;
  color: #28293e;
  }
  #buttonRight:hover {
    border-radius: 5px;
    }
  #buttonLeft { 
    background-color: #d46453;
    border-radius: 5px;
  }
  #buttonRight {  
    background-color: #28293e;
    margin-left: 2%;
    font-weight: 500;
  }


/////////////////
//Rigth Column//
/////////////////

  #rigthColumn {
    background-image: url("../Images/backgroundOval.png");
    background-repeat: no-repeat;
    background-size: 58%;
    background-position-y: 20%;
    background-position-x: 25%;
    max-width: 90%;
    margin-left: 10%;
    margin-top:5%;
  }

  @mixin rectangle($color) {
    height: 41.3%;
    width: 33%;
    background-color: #555;
    border-top-left-radius: 120px;
    border-top-right-radius: 120px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    background-color: $color;
  }

  #rectanglePC {
    @include rectangle(#b550f8);
    img {
      width: 115%;
      margin-top: 7%;
      margin-left: 7%;
    }
  }

  #ovalStars {
    position: relative;
    left: 47%;
    bottom: 38%;
    width: 13%
  }

  #cloud {
    position: relative;
    left: 60%;
    bottom: 35%;
    width: 13%
  }

  #arrow {
    position: relative;
    left: 23%;
    top: 25%
  }

  #curvyArrow {
    position: relative;
    right: 40%;
    
  }

  #card {
    position: relative;
    right: 53%;
    top: 19%;
  }

  #rectangleGirl {
    @include rectangle(#f7cc47); 
    background-image: url("../Images/rectangleGirl.png");
    background-repeat: no-repeat;
    background-position-y:bottom;
    margin-left: 80%;
    background-size: contain;
    position: relative;
    right: 30%;
    bottom: 33%
  }
}

///////////////
/////Media/////
///////////////

@media (max-width:1480px) {
  #section2 {
    text-align: center;
    h3 {
      font-size: 16px;
    }

    h2 {
      font-size: 60px;
      margin-bottom: 40px;
    }

    p {
      font-size: 17px;
    }

    button {
      margin-top: 40px;
    }
    #selfieMetaContainer {
      display: block;
    }
  }
  #rigthColumn {
    position: absolute;
    z-index: -1;
  }
  #rectanglePC,
  #ovalStars,
  #cloud,
  #arrow,
  #curvyArrow,
  #card {
    display: none;
  }

}