#containerSectionOne,
#logoContainer,
#contactButton,
#menu {
  margin: 0 auto;
  max-width: 1800px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}

#containerSectionOne {
  h1{
    margin-left: 10%;
    font-weight: 400;
    user-select: none;
  }
  #menu,
  #contactButton {
    margin-top: 1%;
    cursor: pointer;
  }
  #menu {
    h3 {
      margin: 0 30% 0 30%;
      font-weight: 400;
    }
    h3:hover {
      color: #ef6d58;
      transition: 0.3s;
    }
  }
  #contactButton {
    width: 138px;
    height: 48px;
    font-size: 14px;
    background-color:#28293e;
    border: solid 1px #606060;
    border-radius: 5px;
    transition: 0.3s;
  }
  #contactButton:hover {
    background-color: #ef6d58;
  }
  #container {
    display: none;
  }
}

@media (max-width:1480px) {
  #logo {
    max-width: 50px;
  }
  #contactButton{
    display: none;
  }

  #menu {
    display: none;
  }
  #hamburgerMenu {
    z-index: 100;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    text-align: left;
    position: fixed;
    top: 10;
    right: 0;
    background-color: #ef6d58;
    width: 90%;
    height: 100%;
    h3 {
      margin-top: 3%;
      font-size: 150%;
      border-bottom: solid white;
      border-width: 1px;
      width: 90%;
      align-self: center;
      cursor: pointer;
    }
  }
  #containerSectionOne {
    display: block;
    h1{
      margin-left: 0;
    }
    //Hamburger MENU//
    #container {
      display: flex;
      #hamburger {
        width: 60px;
        height: 60px;
        display: flex;
        justify-content: center;
        align-items: center;
        position: fixed;
        right: 1%;
        top: 1.5%;
        }
      
      #hamburger-init {
        -webkit-appearance: none;
        -moz-appearance: none;
        position: fixed;
        width: 100%;
        height: 100%;
        z-index: 2;
        outline: none;
      }
      
      #menuMobile {
        position: relative;
        width: 50%;
        height: 30%;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
      }
      
      #bar1,
      #bar2,
      #bar3 {
        cursor: pointer;
        margin-bottom: 3px;
        height: 90%;
      }

      #menuMobile #bar1,
      #menuMobile #bar2,
      #menuMobile #bar3 {
        width: 100%;
        background: #ef6d58;
        transition: all .3s;
      }
      
      #hamburger-init:checked + #menuMobile #bar1 {
        transform: translateY(12px) rotate(45deg);
      }
      
      #hamburger-init:checked + #menuMobile #bar2 {
        opacity: 0;
      }
      
      #hamburger-init:checked + #menuMobile #bar3 {
        transform: translateY(0px) rotate(-45deg);
      }
    }
  }
}



